<template>
	<view class="box">
		<view class="user-list">
			<view class="user-sign" v-for="(item,index) in userList">
				<view class="user">
					<image :src="`https://farm.juzhentech.com${item.Image}`" mode=""></image>
					<text class="name">{{item.NickName}}</text>
					<image :src="item.Gender==='男'? man : woman" mode=""></image>
				</view>
				<view class="sign-time">{{item.ScanningTime}}签到</view>
			</view>
		</view>
		<view class="bottom">
			<view class="btn" @click="handleScanning">扫码核销</view>
		</view>
	</view>
</template>

<script>
	import man from '@/static/img/icon_male@3x.png'
	import woman from '@/static/img/icon_female@3x.png'
	import {
		GetActivitySignScanningAjax,
		GetActivitySignDetailAjax
	} from '@/apis/api/modules/activity'
	export default {
		data() {
			return {
				man,
				woman,
				userList: [],
				activityId: ''
			}
		},
		onLoad(e) {
			this.activityId = e.activityId
			this.getData()
		},
		methods: {
			async getData() {
				const result = await GetActivitySignDetailAjax({
					ActivityID: this.activityId,
					token: true
				})
				console.log('签到数据', result.Result)
				this.userList = result.Result
			},
			handleScanning() {
				uni.scanCode({
					success: async (res) => {
						console.log('条码', res.result)
						const result = await GetActivitySignScanningAjax({
							SignID: res.result,
							token: true
						})
						console.log('ma', result)
						uni.showToast({
							title: '成功！',
							icon: 'success'
						})
						this.getData()
					}
				})
			},
		}
	}
</script>

<style scoped lang="scss">
	.box {
		height: 100%;
		padding: 0 30rpx;
		display: flex;
		flex-direction: column;
	}

	.user-list {
		flex: 1;
		overflow: auto;
	}

	.user-sign {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 30rpx;
		border-bottom: 2rpx solid #efefef;
	}

	.user {
		display: flex;
		align-items: center;

		image:nth-of-type(1) {
			width: 60rpx;
			height: 60rpx;
			border-radius: 50%;
		}

		.name {
			padding: 0 14rpx 0 20rpx;
		}

		image:nth-of-type(2) {
			width: 37rpx;
			height: 37rpx;
		}
	}

	.sign-time {
		font-size: 30rpx;
		font-weight: 400;
		color: #999999;
	}

	.bottom {
		margin: 30rpx auto;
		text-align: center;

		.btn {
			width: 310rpx;
			height: 100rpx;
			background: #00D08F;
			border-radius: 52px;
			backdrop-filter: blur(20rpx);
			line-height: 100rpx;
			font-size: 28rpx;
			font-weight: 500;
			color: #FFFFFF;
		}
	}
</style>
